.catalog_container {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 1024px;
  padding: 50px 40px;
}

.card {
  display: flex;
  flex-direction: column;
  padding: 16px;
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  margin-bottom: 46px;
  margin-right: 48px;
}

.card:nth-child(4n) {
  margin-right: 0;
}

.card:nth-child(n+4) {
  margin-bottom: 0;
}

.card_product-name {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #060b35;
  margin-bottom: 4px;
  margin-block-start: 0;
}

.card_product-code {
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: #616070;
}

.card_product-image {
  width: 160px;
  height: 134px;
  margin: 16px auto 40px;
}

.card_product_price {
  display: flex;
  justify-content: space-between;
}

.stars_section {
  display: flex;
  justify-content: space-between;
  margin-bottom: 24px;
  margin-top: 16px;
}

.stars {
  display: flex;
  flex-direction: row;
}

.star {
  width: 16px;
  height: 16px;
  margin-right: 4px;
  background: url("../images/star.svg") no-repeat center;
}

.stars--1 .star:nth-child(-n+1),
.stars--2 .star:nth-child(-n+2),
.stars--3 .star:nth-child(-n+3),
.stars--4 .star:nth-child(-n+4),
.stars--5 .star:nth-child(n) {
  background-image: url("../images/star-active.svg");
}

.stars_reviews {
  display: flex;
  align-items: flex-end;
  font-size: 10px;
  line-height: 14px;
  color: #060b35;
}

.price {
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #616070;
}

.number {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
}

.card_product-buy-button {
  display: flex;
  background-color: #00acdc;
  color: white;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  border: 1px solid transparent;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  justify-content: center;
  align-items: center;
  margin-top: 16px;
}

.card_product-buy-button:hover {
  background-color: white;
  color: #00acdc;
  border-color: #00acdc;
}
